import React from 'react'
import styled from 'styled-components'
import { Row, Col } from 'antd'
import { useSelector } from 'redux/hook'
import Container from './Container'
import NavberMobile from './NavberMobile'
import NavberDesktop from './NavberDesktop'

const NavberHeader = styled.header`
  overflow: hidden;
  position: fixed;
  z-index: 2;
  width: 100%;
  height: 50px;
  line-height: 50px;
  background: ${({ theme }) => theme.colors.navbarBg};
  border-bottom: 1px solid #eaedf3;
  box-shadow: 0px 4px 4px 0px rgba(12, 0, 46, 0.01);
  .ant-menu-horizontal {
    color: #5062aa;
    background: transparent;
    line-height: 50px;
    border-bottom: 0;
    float: right;
    li {
      border-bottom: 0;
    }
  }
  .ant-menu-horizontal > .ant-menu-item:hover,
  .ant-menu-horizontal > .ant-menu-submenu:hover,
  .ant-menu-horizontal > .ant-menu-item-active,
  .ant-menu-horizontal > .ant-menu-submenu-active,
  .ant-menu-horizontal > .ant-menu-item-open,
  .ant-menu-horizontal > .ant-menu-submenu-open,
  .ant-menu-horizontal > .ant-menu-item-selected,
  .ant-menu-horizontal > .ant-menu-submenu-selected {
    border-bottom: 0;
    color: #d0115c;
  }
`

const Navber = () => {
  const getIsMobile = useSelector((state) => state.get('app').get('isMobileState'))
  return (
    <NavberHeader>
      <Container>
        <Row gutter={14}>
          <Col>{getIsMobile ? <NavberMobile /> : <NavberDesktop />}</Col>
        </Row>
      </Container>
    </NavberHeader>
  )
}
export default Navber
